* {
    margin: 0;
    padding: 0;
}
body {
    background-color: #e2fbfe;
    height: 100vh;
}
#app {
    aspect-ratio: 2 / 1;
    background-color: #326061;
    border-radius: 10px;
    box-shadow: 0 0 20px #000;
    left: 50%;
    top: 50%;
    align-items: center;
    display: flex;
    justify-content: center;
    position: fixed;
    transform: translate(-50%, -50%);
    width: 80vw;
    max-width: 500px;
}
#quote-box {
    color: #fff;
    text-align: center;
}
#text {
    font-size: 1.3em;
    font-family: monospace;
    padding: 20px;
}
#author {
    text-align: right;
    padding: 20px;
    margin: auto auto;
    max-width: 400px;
    font-size: 1.5em;
    font-family: cursive;
}
#action-container {
    align-items: center;
    display: flex;
    padding: 15px;
    justify-content: space-between;
}
#tweet-quote,
#new-quote {
    text-decoration: none;
    border-radius: 10px;
    color: #000;
    background-color: #00ffff;
    padding: 10px 20px;
    transition: background-color 300ms ease-in-out;
}
#new-quote {
    border: none;
    cursor: pointer;
}
#tweet-quote:hover,
#tweet-quote:focus,
#new-quote:focus,
#new-quote:hover {
    background-color: #00d0d0;
}